.App{ 
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;  
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
}

.sum {
    display:  flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    width: 100%;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.logo{
    padding-top: 0.5em;
    margin-left: 2rem;
    color: white;
    font-size: 2em;
    font-weight: bolder;
    height: 2em;
}
.logo-link{
    cursor: pointer;
}
.help{
    padding-top: 0.5em;
    margin-right: 2rem;
    color: white;
    font-size: 2em;
    font-weight: bolder;
    height: 2em;
}
.ul {
