        width: 700px;
    }
}
#roombutton{
    background-color:rgb(0, 217, 255);
    border: none;
    color: rgb(3, 3, 3);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 1em;
}
#roombutton:hover{
    color:teal;
}
h2{
    color: white;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-weight: bolder;
}
.accessibility{
    border: 0; 
    clip: rect(0 0 0 0); 
    height: 1px; 
    margin: -1px; 
    overflow: hidden; 
    padding: 0; 
    position: absolute; 
    width: 1px; 
}
.flex-container {
    display: flex;
    height: 40vh;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.flex-inner{
    display: flex;
    height: 10em;
    width: 25em;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 0.3em;
    margin: 8em;
    color: black;
}

@media only screen and (max-width:900px) and (min-width:700px){
    .flex-inner{
        display: flex;
        height: 1em;
    }
}

@media only screen and (max-width:700px){
    .flex-inner{
        display: flex;
        height: 1em;
        border-radius: 0.3em;
        margin: 4em;
    }
}

.Room{
    background-color:rgb(0, 217, 255);   
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: 
        "header header  header" 
        "users  video   video" 
        "chat   .   ." 
        "footer footer  footer";
}
.header{
    grid-area: header; 
}
.video {
    grid-area: video;
    display: flex;
    justify-content: center;
    align-self: center;
 
    background-color: rgb(26, 25, 25);
    border: rgb(0, 0, 0) 1px solid;
}
.chat{
    grid-area: chat;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 25, 25);
    border: rgb(68, 68, 68) 1px solid;
    border: none;
    list-style: none;
}
.users{
    grid-area: users;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 25, 25);
    border: rgb(68, 68, 68) 1px solid;
}
.footer{
    grid-area: footer;
}
h1 {
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-style: bold;
    margin: 1em;
} 

@media only screen and (max-width:700px){
    .Room{
        grid-template-rows: 5vh, 5vh, 65vh, 25vh, 3vh;
        grid-gap: 0.5rem;
        grid-template-areas: "header" "title" "video" "chat" "footer";
    }
    .video{
        grid-area: video;
        height: 50vh;
    }
    .chat{
        margin-top: 0;
        grid-area: chat;
        justify-content: flex-start;
    }
    h1{
        margin: 0;
    }
}
