.modal-btn {
    background-color: white;
    color: black;
    width: 30px;
    height: 25px;
    right: 10px;
    top: 10px;
    position: fixed;
    font-size: larger;
    font-weight: bold;
}

.modal .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
}

.overlay {
    background-color: rgba(40, 40, 40, 0.8);
    z-index: 2;
}

.modal-content {
    z-index: 3;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}
