}

.roomlist{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-rows: 2rem;
    list-style: none;
}

.flex-rooms{
    display: flex;
    width: 100%;
    height: 30vh;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 1em;
    border-radius: 0.3em;
    margin: 1rem;
    margin-top: 15vh;
    margin-left: 0;
    background-color: rgb(0, 0, 0);
}
.flex-room{
    display: flex;
    width: 95%;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 1em;
    border-radius: 0.3em;
    margin: 1rem;
    margin-left: -2vh;
    background-color: rgb(53, 53, 53);
}

@media only screen and (max-width:900px) and (min-width:700px){
    .roomlist{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-auto-rows: 2.5rem;
    }
    .flex-room{
        display: flex;
        width: 80%;
    }
}

@media only screen and (max-width:700px){
    .roomlist{
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: 2rem;
    }
    .flex-rooms{
        padding: 1em;
        margin: 1rem;
    }
    .flex-room{
        display: flex;
        width: 300px;
        padding: 1em;
        margin: 0rem;
    }
}
